<template>
  <header class="navbar">
    <div class="top-bar">
      <nav class="container">
        <button id="burger" @click="toggleValue">
          <i class="large material-icons">{{ icon }}</i>
        </button>
        <h1 class="logo">alfanme<span>.</span></h1>
        <button class="hire">
          Hire me
        </button>
      </nav>
    </div>
    <Sidebar v-show="show" />
  </header>
</template>

<script>
import Sidebar from "./Sidebar.vue";

export default {
  name: "Navigation",
  components: {
    Sidebar,
  },
  data() {
    return {
      icon: "menu",
      show: false,
    };
  },
  methods: {
    toggleValue() {
      this.show = !this.show;
      this.icon = this.show ? "close" : "menu";
      document.querySelector("html").style.overflow = this.show
        ? "hidden"
        : "scroll";
    },
  },
};
</script>

